import React from 'react';
import PropTypes from 'prop-types';

import Panel from '@vkontakte/vkui/dist/components/Panel/Panel';
import PanelHeader from '@vkontakte/vkui/dist/components/PanelHeader/PanelHeader';
import Group from '@vkontakte/vkui/dist/components/Group/Group';


import { Icon28NewsfeedOutline, Icon28MessageOutline, Icon28SettingsOutline} from '@vkontakte/icons'

const Settings = props => (
	<Panel id={props.id}>
		<PanelHeader>
			Настройки
		</PanelHeader>
        <Group>
            Еще покачто ничого
        </Group>
        <Tabbar>
            <TabbarItem text="Главная" onClick={props.go} data-to="home">
                <Icon28NewsfeedOutline />
            </TabbarItem>
            <TabbarItem text="Персик" onClick={props.go} data-to="persik">
                <Icon28MessageOutline />
            </TabbarItem>
            <TabbarItem text="Настройки">
                <Icon28SettingsOutline />
            </TabbarItem>
        </Tabbar>
	</Panel>
);

Settings.propTypes = {
	id: PropTypes.string.isRequired,
	go: PropTypes.func.isRequired,
};

export default Settings;
